function Connexion() {
  const [show, setShow] = useState(false);
  const [email, setEmail] = useState("");
  const [statut, setStatut] = useState("");
  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  const addNewPreInscription = (e) => {
    e.preventDefault();
    const data = {
      email: email,
      statut: statut,
    };
    const headers = new Headers({
      "X-Requested-With": "XMLHttpRequest",
      "Content-Type": "application/json",
    });

    const options = {
      method: "POST",
      body: JSON.stringify(data),
      headers: headers,
    };
    fetch(
      "https://back-end.osc-fr1.scalingo.io/vitrine/preInscription",
      options
    )
      .then((response) => {
        return response.json();
      })

      .then(
        (responseObject) => {
          console.log(email);
        },

        (error) => {
          console.log(error);
        }
      );
  };

  return (
    <>
      <Button className="buttonPreInscr" onClick={handleShow}>
        Je m'inscris{" "}
        <Button onClick={handleShow} className="flecheInscr">
          {">"}
        </Button>
      </Button>

      <Modal
        show={show}
        onHide={handleClose}
        animation={true}
        backdrop="static"
        keyboard={false}
      >
        <Modal.Header closeButton>
          <Modal.Title>Pré-inscription</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Image className="imgModal" src="/images/LogoOK.png" />

          <Form.Group className="index">
            <Form.Control
              className="inputModal"
              placeholder="email"
              name="email"
              onChange={(e) => {
                setEmail(e.target.value);
              }}
              required
            />
            <Form.Check
              className="CheckModal"
              name="restaurateur"
              value="restaurateur"
              label="Je suis restaurateur"
              onChange={(e) => {
                setStatut(e.target.value);
              }}
            />
            <Form.Check
              className="CheckModal"
              name="serveur"
              value="serveur"
              label="Je suis serveur"
              onChange={(e) => {
                setStatut(e.target.value);
              }}
            />
          </Form.Group>
        </Modal.Body>
        <Modal.Footer>
          <Button
            className="modalButton"
            variant="secondary"
            onClick={handleClose}
          >
            Fermer
          </Button>
          <Button
            className="modalButton"
            variant="primary"
            onClick={addNewPreInscription}
          >
            Je m'inscris
          </Button>
        </Modal.Footer>
      </Modal>
    </>
  );
}

function CookiePermission() {
  const [show, setShow] = useState(false);
  const [cookieValid, setCookieValid] = useState(false);

  const handleCloseValid = () => {
    setCookieValid(true);
    localStorage.setItem("@valid", cookieValid);
    console.log(cookieValid);
    setShow(false);
  };
  const handleClose = () => {
    setShow(false);
  };

  const validCookie = () => setCookieValid(true);
  const denyCookie = () => setCookieValid(false);

  useEffect(() => {
    if (!localStorage.getItem("@valid") === false) {
      setShow(true);
    } else {
      console.log("coucou");
    }
  }, []);

  return (
    <>
      <Modal
        show={show}
        onHide={handleClose}
        animation={false}
        centered
        backdrop="static"
      >
        <Modal.Header>
          <Modal.Title>Information sur les cookies</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          Le Site Tipourboire utilise différents cookies afin d’améliorer ses
          services et effectuer des suivis d’audience. Certains cookies sont
          indispensables au fonctionnement du Site. Vous pouvez accepter ces
          cookies, les refuser, ou gérer vos préférences.
          <br />
          <Link to="CGU/CGV_Tipourboire.pdf" target="_blank">
            <button className="triggerModal">Notre politique de cookie</button>
          </Link>
        </Modal.Body>
        <Modal.Footer>
          <button className="triggerModal" onClick={handleCloseValid}>
            Tout Accepter
          </button>
          <button className="triggerModal" onClick={handleClose}>
            Tout Refuser
          </button>
        </Modal.Footer>
      </Modal>
    </>
  );
}

